import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, compareJSON, fetchEncryptStream } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt, DropDwnPicker_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import { AddBusiness_Cmpt } from "..";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const DropDwnPicker = DropDwnPicker_Cmpt();



const AddBusiness = AddBusiness_Cmpt();



const frmNm = "upd_user"

const UpdUser  = ({data, confirm}) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};

	let flds = [
		"businessId",
		"isOwner",
		"role",
		"isqvmAgent",
		"telegram_chat_id",
		"isDelivery",
		"allowInventory",
		"phoneNumber",
		"state","city",
		"active",
		"serviceState", 
		"serviceCity",
		"hasNotaryAccess",
		"hasSuperUserAccess",
	]

	
	const save  = async ()  => {
		let ffm = getStore(frmNm);
		let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )

		
		let qqq = {...diff.data}
		/**
		qqq["state"] = "Holguin"
		qqq["serviceState"] = "Holguin"
		qqq["serviceCity"] = "80100"
		qqq["city"] = "80100"
		*/
		
		let bdyq2 = {
			query: "updateUserDetails",
			params:{
				"userId": ffm["userId"],
				
			},
			data2update: qqq
		};
		//console.log(bdyq2)
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			updStore(frmNm + "_bck", {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
	};



	let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )

	
	const updObs = (f) => {
		set_obs(gen16CodeId())
	}

	const clean = (f) => {
		updStore("upd_user", data);
		updStore("upd_user_bck", JSON.parse(JSON.stringify(data)));
		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = <AddBusiness confirm={updObs} />
		
		OpenModal(modaldata);
	}


	return (
		<div
		style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}
		>
			<div className="_dsplFlx ">
				<p className="form_title">
					Actualizar Usuario
				</p>
				<div className="flexSpace"/>
				<p className="group_title addBtn" onClick={clean}>
					Add Business
				</p>
			</div>

			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<div className="_dsplFlx " style={{padding: "18px"}}>
					<div className="flexSpace"/>

					<DropDwnPicker 
						init={getStore(frmNm)?.["businessId"]}
						lbl_empty={"Business Name"} 
						list={getStore("bussines_list")} 
						onSelect={(e)=>updFrm("businessId",e)}
					/>
					<div className="flexSpace"/>
				</div>
				<div className="_dsplFlx " style={{padding: "18px"}}>
					<div className="flexSpace"/>
					<DropDwnPicker 
						init={getStore(frmNm)?.["role"]}
						lbl_empty={"Roles"} 
						list={[
							{label: "Owner", id:"owner"},
							{label: "Lectura y Escritura", id:"write_read"},
							{label: "Solo Lectura", id:"read_only"},
						]} 
						onSelect={(e)=>updFrm("role",e)}
						direction={"right"}
					/>
					<div className="flexSpace"/>
				</div>
			</div>


			<div className="_dsplFlx acc_dtls">
				<div className=""  >
					<div className="code" >
						{getStore(frmNm)?.["name"]} 
					</div>
					<div className="name" style={{marginTop: 9}}>
						{getStore(frmNm)?.["email"]}
					</div>
				</div>
			</div>

			<div className="_dsplFlx " style={{marginTop:35}}>
				<span className={`checklabel`}>
					{"Qvamarket Agente"}
				</span>
				<CheckBoxSlide
					initvalue={getStore(frmNm)?.["isqvmAgent"]}
					keyCode={77}
					updChange={(e)=>updFrm("isqvmAgent",e)}
				/>
				<span className={`checklabel`}>
				
				</span>
			</div>

			<div className="_dsplFlx ">
				<span className={`checklabel`}>
					{"Qvamarket Inventario"}
				</span>
				<CheckBoxSlide
					initvalue={getStore(frmNm)?.["allowInventory"]}
					keyCode={79}
					updChange={(e)=>updFrm("allowInventory",e)}
				/>
			</div>

			<div className="_dsplFlx ">
				<span className={`checklabel`}>
					{"Agente Activo"}
				</span>
				<CheckBoxSlide
					initvalue={getStore(frmNm)?.["active"]}
					keyCode={73}
					updChange={(e)=>updFrm("active",e)}
				/>
			</div>
			<div className="_dsplFlx ">
				<span className={`checklabel`}>
					{"Acceso a Notaria"}
				</span>
				<CheckBoxSlide
					initvalue={getStore(frmNm)?.["hasNotaryAccess"]}
					keyCode={36}
					updChange={(e)=>updFrm("hasNotaryAccess",e)}
				/>
			</div>

			<div className="_dsplFlx ">
				<span className={`checklabel`}>
					{"Supervisor"}
				</span>
				<CheckBoxSlide
					initvalue={getStore(frmNm)?.["hasSuperUserAccess"]}
					keyCode={36}
					updChange={(e)=>updFrm("hasSuperUserAccess",e)}
				/>
			</div>
			
			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{diff?.hasChange ?
				<div className="card">
					<button onClick={save}>
						Actualizar
					</button>
				</div>
				:null}
			</div>
		

			
		</div>
	);
};

export default UpdUser;

/** 


"id","name","email",

"isqvmAgent",
"telegram_chat_id",
"isDelivery",
"allowInventory",
"phoneNumber",
"state","city",
"active",
"serviceState", 
"serviceCity"

*/